<template>
    <aside id="skeletons">
        
        <template  v-if="typeValue =='list' ">
            <ul class="skeleton--list">
                <li v-for="item in 3"></li>
            </ul>
        </template>
        <template v-else>
            <div class="skeleton--card"></div>
        </template>
    </aside>
</template>
    
<script>
  import { ref, onMounted, toRefs, reactive, defineProps  } from 'vue';
  export default {
    name: 'Skeleton',
    props: {
        SkeletorType:{
          type:String,
        }
    },
    setup( props ) {
        console.log( props, props.SkeletorType );
        const typeValue = ref('');
        typeValue.value = props.SkeletorType;

        return{ 
            typeValue
        }
    }
  }
  </script>
    
<style lang="scss" scoped>
#skeletons{
    display: block;
    .skeleton{
        display: block;
        width:100%;
        height:100%;

        &--card{
            display: block;
            width:100%;
            height:100%;
            background: #f4f4f4;
        }
        &--list{
            margin-bottom: vw(100px);
            li{
                display: block;
                width:100%;
                height:vw(300px);
                margin-top: vw(30px);
                background: linear-gradient(120deg, #e5e5e5 30%, #f0f0f0 38%, #f0f0f0 40%, #e5e5e5 48%);
                border-radius: 1rem;
                background-size: 200% 100%;
                background-position: 100% 0;
                animation: load 1s infinite;
            }
        }
    }
}
@keyframes load {
    100% {
        background-position: -100% 0;
    }
}





